<template>
  <Header :title="activeName" />
  <div class="content-container" v-loading.fullscreen.lock="loading">
    <div class="login-form">
      <div class="center-block">
        <SignIn v-if="activeName === '登录'" @onSuccess="goBack" />
        <SignUp v-else @onSuccess="goBack" />
        <p v-if="activeName === '登录'" class="login-tab_nav">
          没有账户？
          <a @click="toggleActiveTab" href="javascript:void(0)">加入</a>
        </p>
        <p v-else class="login-tab_nav">
          已有账户？
          <a @click="toggleActiveTab" href="javascript:void(0)">登录</a>
        </p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage } from 'element-plus';
import { exceptionCapture, errorMessage } from '@/utils';
import { useSignInMutation, useSignUpMutation } from '@/graphql';
import store, { Storage, TYPES } from '@/store';
import SignIn from './parts/SignIn.vue';
import SignUp from './parts/SignUp.vue';

const TabName = {
  signIn: '登录',
  signUp: '注册'
};

export default defineComponent({
  components: {
    SignIn,
    SignUp
  },
  setup(props, context) {
    const router = useRouter();
    const activeName = ref(TabName.signIn);

    function goBack() {
      router.go(-1);
    }
    function toggleActiveTab() {
      const tabName = activeName.value === TabName.signIn ? TabName.signUp : TabName.signIn;
      activeName.value = tabName;
    }

    return {
      activeName,
      toggleActiveTab,
      goBack
    };
  }
});
</script>

